<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-使用指令1">使用指令</a></li><li><a href="#vue-doc-位置1">位置</a></li><li><a href="#vue-doc-双向绑定1">双向绑定</a></li><li><a href="#vue-doc-嵌套复杂内容1">嵌套复杂内容</a></li><li><a href="#vue-doc-确认框1">确认框</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-气泡提示1" class="vue-doc-element">气泡提示</h1>
<p class="vue-doc-element">null</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h1>
<p class="vue-doc-element">Button</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demodirective></demodirective>
<demoplacement></demoplacement>
<demomodel></demomodel>
<demoslot></demoslot>
<democomfirm></democomfirm>
        </div>


        <div class="vue-doc-content">
          <p class="vue-doc-element">Poptip 与 Tooltip 类似，具有很多相同配置，不同点是 Poptip 以卡片的形式承载了更多的内容，比如链接、表格、按钮等。</p>
<p class="vue-doc-element">Poptip 还 confirm 确认框，与 Modal 不同的是，它会出现在就近元素，相对轻量。</p>
<h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-poptipprops1_1" class="vue-doc-element">Poptip props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>trigger</td>
<td>触发方式，可选值为hover（悬停）click（点击）focus（聚焦）,在 confirm 模式下，只有 click 有效</td>
<td>String</td>
<td>click</td>
</tr>
<tr>
<td>title</td>
<td>显示的标题</td>
<td>String / Number</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>显示的正文内容，只在非 confirm 模式下有效</td>
<td>String / Number</td>
<td>空</td>
</tr>
<tr>
<td>placement</td>
<td>提示框出现的位置，可选值为toptop-starttop-endbottombottom-startbottom-endleftleft-startleft-endrightright-startright-end，2.12.0 版本开始支持自动识别</td>
<td>String</td>
<td>top</td>
</tr>
<tr>
<td>width</td>
<td>宽度，最小宽度为 150px，在 confirm 模式下，默认最大宽度为 300px</td>
<td>String / Number</td>
<td>-</td>
</tr>
<tr>
<td>confirm</td>
<td>是否开启对话框模式</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>ok-text</td>
<td>确定按钮的文字，只在 confirm 模式下有效</td>
<td>String</td>
<td>确定</td>
</tr>
<tr>
<td>cancel-text</td>
<td>取消按钮的文字，只在 confirm 模式下有效</td>
<td>String</td>
<td>取消</td>
</tr>
<tr>
<td>transfer</td>
<td>是否将弹层放置于 body 内，在 Tabs、带有 fixed 的 Table 列内使用时，建议添加此属性，它将不受父级样式影响，从而达到更好的效果</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>popper-class</td>
<td>给 Poptip 设置 class-name，在使用 transfer 时会很有用</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>options</td>
<td>自定义 popper.js 的配置项，具体配置见 popper.js 文档</td>
<td>Object</td>
<td>{ modifiers: { computeStyle:{ gpuAcceleration: false, }, preventOverflow :{ boundariesElement: &#39;window&#39; } } }</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-poptipevents1_2" class="vue-doc-element">Poptip events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-popper-show</td>
<td>在提示框显示时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-popper-hide</td>
<td>在提示框消失时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-ok</td>
<td>点击确定的回调，只在 confirm 模式下有效</td>
<td>无</td>
</tr>
<tr>
<td>on-cancel</td>
<td>点击取消的回调，只在 confirm 模式下有效</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-poptipslot1_3" class="vue-doc-element">Poptip slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>主体内容</td>
</tr>
<tr>
<td>title</td>
<td>提示框标题，定义此 slot 时，会覆盖 props title</td>
</tr>
<tr>
<td>content</td>
<td>提示框内容，定义此 slot 时，会覆盖 props content，只在非 confirm 模式下有效</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demodirective from './demo/directive.md';
import demoplacement from './demo/placement.md';
import demomodel from './demo/model.md';
import demoslot from './demo/slot.md';
import democomfirm from './demo/comfirm.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demodirective,demoplacement,demomodel,demoslot,democomfirm
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


